import React, { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/app/components/ui/dialog';
import { Button } from '@/app/components/ui/button';
import { Input } from '@/app/components/ui/input';
import Link from 'next/link';
import { Menu, X, Star, User } from 'lucide-react';

// Define types for navigation items
interface NavItem {
  name: string;
  href: string;
}

// Define types for AuthForm props
interface AuthFormProps {
  isLogin: boolean;
}

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isLoginMode, setIsLoginMode] = useState(true);

  const navItems: NavItem[] = [
    { name: 'Home', href: '/' },
    { name: 'Services', href: '#services' },
    { name: 'About', href: '#about' },
    { name: 'Contact', href: '#contact' },
  ];

  const AuthForm: React.FC<AuthFormProps> = ({ isLogin }) => (
    <form className="space-y-4 mt-4">
      {!isLogin && (
        <Input
          type="text"
          placeholder="Full Name"
          className="w-full"
        />
      )}
      <Input
        type="email"
        placeholder="Email"
        className="w-full"
      />
      <Input
        type="password"
        placeholder="Password"
        className="w-full"
      />
      <Button className="w-full bg-purple-600 hover:bg-purple-700">
        {isLogin ? 'Login' : 'Sign Up'}
      </Button>
      <p className="text-center text-sm text-gray-600">
        {isLogin ? "Don't have an account? " : "Already have an account? "}
        <button
          type="button"
          onClick={() => setIsLoginMode(!isLogin)}
          className="text-purple-600 hover:underline"
        >
          {isLogin ? 'Sign Up' : 'Login'}
        </button>
      </p>
    </form>
  );

  return (
    <header className="fixed w-full bg-white/80 backdrop-blur-sm z-50 top-0 left-0 right-0 border-b border-gray-200">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          {/* Logo */}
          <Link href="/" className="flex items-center space-x-2">
            <Star className="w-8 h-8 text-purple-600" />
            <span className="text-xl font-bold text-purple-600">Parasara Jyotish</span>
          </Link>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-8">
            {navItems.map((item) => (
              <Link
                key={item.name}
                href={item.href}
                className="text-gray-600 hover:text-purple-600 transition-colors"
              >
                {item.name}
              </Link>
            ))}
          </nav>

          {/* Auth Buttons */}
          <div className="hidden md:flex items-center space-x-4">
            <Dialog>
              <DialogTrigger asChild>
                <Button variant="outline">Login</Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>{isLoginMode ? 'Welcome Back' : 'Create Account'}</DialogTitle>
                </DialogHeader>
                <AuthForm isLogin={isLoginMode} />
              </DialogContent>
            </Dialog>

            <Dialog>
              <DialogTrigger asChild>
                <Button className="bg-purple-600 hover:bg-purple-700 text-white">Sign Up</Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>{isLoginMode ? 'Welcome Back' : 'Create Account'}</DialogTitle>
                </DialogHeader>
                <AuthForm isLogin={false} />
              </DialogContent>
            </Dialog>
          </div>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
          >
            {isMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
          </button>
        </div>
      </div>

      {/* Mobile Menu */}
      {isMenuOpen && (
        <div className="md:hidden border-t border-gray-200 bg-white">
          <div className="container mx-auto px-4 py-4 space-y-4">
            {navItems.map((item) => (
              <Link
                key={item.name}
                href={item.href}
                className="block text-gray-600 hover:text-purple-600 transition-colors"
                onClick={() => setIsMenuOpen(false)}
              >
                {item.name}
              </Link>
            ))}
            <div className="space-y-2 pt-4 border-t border-gray-200">
              <Button variant="outline" className="w-full" onClick={() => setIsMenuOpen(false)}>
                Login
              </Button>
              <Button className="w-full bg-purple-600 hover:bg-purple-700" onClick={() => setIsMenuOpen(false)}>
                Sign Up
              </Button>
            </div>
          </div>
        </div>
      )}
    </header>
  );
};

export default Header;